@font-face {
  font-family: "Lexend";
  font-style: normal;
  font-weight: 400;
  src: url("../../src/fonts/Lexend-Regular.ttf") format("truetype");
}
@font-face {
  font-family: "Lexend";
  font-style: normal;
  font-weight: 700;
  src: url("../../src/fonts/Lexend-Bold.ttf") format("truetype");
}
@font-face {
  font-family: "Lexend";
  font-style: normal;
  font-weight: 900;
  src: url("../../src/fonts/Lexend-Black.ttf") format("truetype");
}
.destinos {
  padding-block: 2.4rem;
}
@media (min-width: 1024px) {
  .destinos {
    padding-block: 4.8rem;
  }
}
.destinos__titulo {
  margin-bottom: 2.4rem;
}
@media (min-width: 1024px) {
  .destinos__titulo {
    margin-bottom: 4.8rem;
  }
}
.destinos__grid {
  max-width: 50rem;
  margin-inline: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-items: center;
  gap: 1.6rem;
}
@media (min-width: 768px) {
  .destinos__grid {
    max-width: unset;
    grid-template-columns: repeat(4, 1fr);
  }
}
.destinos__destino {
  text-align: center;
}
.destinos__img {
  width: 15rem;
  height: 15rem;
  margin-inline: auto;
}